
import { IFullCityData } from '../interfaces/weather-data.interface'
import moment from 'moment'
export default {
  props: {
    cityData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {

    }
  },
  computed: {
    tempType(): string {
      return this.$store.getters.tempType;
    },
    setRandomImageClass() {
      console.log("🚀 ~ file: favorite-card.cmp.vue ~ line 77 ~ setRandomImageClass ~ this.getRandomInt(4)", this.getRandomInt(4))
      return `img-${this.getRandomInt(4)}`;
    }
  },
  methods: {
    skipDay(num: number) {
      return moment().add(num, 'day').format('ddd')
    },
    convertToC(num: number) {
      let c = (num - 32) * 5 / 9
      return Math.floor(c);
    },
    setCurrCard(){
      this.$store.commit('setCurrCard', {cityWeather: this.cityData})
      this.$router.push('/')
    },
    removeFromFavorites(){
      this.$store.dispatch({type:'toggleFavorite',currCard: this.cityData})

    },
    setBgcColor() {
      let num = this.$store.getters.colorNum;
      console.log("🚀 ~ file: weather-page.cmp.vue ~ line 126 ~ setBgcColor ~ num", num)
      return (num <= 5 || num >= 20) ? 'darkTheme' : 'lightTheme';
    },
    getRandomInt(max:number) {
      return Math.floor(Math.random() * max);
    }
  },
}
